import { Directive } from '@angular/core';
import { Observable } from 'rxjs';

import { Loan } from '@backbase/loans-collection-ui-ang';

import { isErrorState, isLoadingState, stateMap } from '../../../utils/call-state.util';
import { LoansStoreService } from '../../../store/loans/store.service';

@Directive()
// eslint-disable-next-line @angular-eslint/directive-class-suffix
export abstract class LoanDetailsBaseComponent<T> {
  /**
   * Loan details steam
   */
  readonly loan$: Observable<Loan | undefined> = this.storeService.loan$;
  /**
   * Flag represents loading state
   */
  readonly isLoading$: Observable<boolean> = this.storeService.loanCallState$.pipe(stateMap(isLoadingState));
  /**
   * Flag represents error state
   */
  readonly isErrorState$: Observable<boolean> = this.storeService.loanCallState$.pipe(stateMap(isErrorState));

  constructor(protected readonly storeService: LoansStoreService) {}

  /**
   * Configuration for component
   */
  abstract getDataConfig({ viewType }: Loan): T;
}
